import { motion } from 'framer-motion'
import { useScrollAnimation } from '../hooks/useScrollAnimation'
import { Link } from 'react-router-dom'; // Add this import

export default function Home() {
  const [ref, controls] = useScrollAnimation()

  return (
    <motion.div
      ref={ref}
      initial="hidden"
      animate={controls}
      variants={{
        visible: { opacity: 1, y: 0 },
        hidden: { opacity: 0, y: 50 }
      }}
      transition={{ duration: 0.5 }}
    >
      <h1 className="text-4xl font-bold mb-6 text-gradient">Welcome to My Portfolio</h1>
      <p className="text-xl mb-4">
        Hi, I'm [Your Name]. I'm a [Your Profession] passionate about creating amazing web experiences.
      </p>
      <p className="text-lg text-text-secondary">
        Explore my projects and get in touch if you'd like to work together!
      </p>
      <motion.div
        className="mt-8"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.5, duration: 0.5 }}
      >
        <Link to="/projects" className="btn">View My Projects</Link>
      </motion.div>
    </motion.div>
  )
}